.certifications {
  padding: 6rem 2rem;
  background: linear-gradient(180deg, rgba(139, 92, 246, 0.05) 0%, rgba(59, 130, 246, 0.05) 100%);
}

.certifications .section-title {
  color: #000000;
}

.certifications .section-subtitle {
  color: #000000;
}

.certifications-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 900px;
  margin: 0 auto;
}

.certification-item {
  display: flex;
  gap: 2rem;
  padding: 2rem;
  background: linear-gradient(135deg, rgba(30, 41, 59, 0.6), rgba(15, 23, 42, 0.6));
  border: 1px solid rgba(148, 163, 184, 0.1);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.certification-item:hover {
  border-color: rgba(139, 92, 246, 0.3);
  background: linear-gradient(135deg, rgba(30, 41, 59, 0.8), rgba(15, 23, 42, 0.8));
  transform: translateX(5px);
}

.cert-icon {
  width: 50px;
  height: 50px;
  min-width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(139, 92, 246, 0.1);
  border-radius: 10px;
  color: #c4b5fd;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

.cert-content {
  flex: 1;
}

.cert-title {
  font-size: 1.2rem;
  font-weight: 700;
  color: #000000;
  margin: 0 0 0.5rem 0;
}

.cert-issuer {
  color: #000000;
  font-size: 0.95rem;
  margin: 0 0 0.75rem 0;
  font-weight: 500;
}

.cert-description {
  color: #000000;
  margin: 0 0 1rem 0;
  line-height: 1.6;
}

.cert-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.skill-badge {
  display: inline-block;
  padding: 0.35rem 0.8rem;
  background: rgba(59, 130, 246, 0.1);
  color: #000000;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.skill-badge:hover {
  background: rgba(59, 130, 246, 0.2);
  border-color: rgba(59, 130, 246, 0.5);
}

@media (max-width: 768px) {
  .certifications {
    padding: 4rem 1rem;
  }

  .certification-item {
    flex-direction: column;
    gap: 1rem;
  }

  .cert-icon {
    margin: 0;
  }
}
